html {
  font-family: Arial, Helvetica, sans-serif;
}

body {
  margin: 0;
}

.container {
  display: flex;
  flex-wrap: wrap ;
}

.box {
  height: 300px;
  flex-basis: 100%;
  background-color: rgb(255, 0, 0);
  color: #fff;
  font-size: 100px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.box--2 {
  background-color: rgb(204, 0, 0);
}

.box--3 {
  background-color: rgb(153, 0, 0);
}

.box--4 {
  background-color: rgb(102, 0, 0);
}

.box--5 {
  background-color: rgb(51, 0, 0);
}

.box--6 {
  background-color: rgb(0, 0, 0);
}

@media (min-width: 600px) {
  .box {
    flex-basis: 50%;
  }

  .box--1 {
    order: 1;
  }

  .box--2 {
    order: 2;
  }

  .box--4 {
    order: 3;
  }

  .box--3 {
    order: 4;
  }

  .box--5 {
    order: 5;
  }

  .box--6 {
    order: 6;
  }
}

@media (min-width: 900px) {
  .box {
    flex-basis: 33.3%;
  }

  .box--1 {
    order: 1;
  }

  .box--2 {
    order: 2;
  }

  .box--4 {
    order: 6;
  }

  .box--3 {
    order: 3;
  }

  .box--5 {
    order: 5;
  }

  .box--6 {
    order: 4;
  }
}
